<template>
  <select
    :value="modelValue"
    class="dropdown-list mb-1"
    @change="$emit('update:modelValue', $event.target.value)"
  >
    <option v-for="(option, i) in options" :key="i" :value="option">
      {{ option }}
    </option>
  </select>
</template>

<script>
export default {
  name: "DropdownList",
  props: {
    modelValue: {
      type: [String, Number],
      required: false
    },
    options: {
      type: Array,
      required: true
    }
  },
  emits: ['update:modelValue']
};
</script>

