<template>
  <div id="app">
    <nav class="shadow p-3 text-light">
      <div
        class="container d-block d-md-flex align-items-center justify-content-between"
      >
        <h1 class="mb-3 mb-md-0">
          RSPB Big Garden Birdwatch
        </h1>
        <div class="d-flex justify-content-between">
          <label for="bird" class="sr-only">Bird:</label>
          <dropdown-list
            id="bird"
            :options="birdOptions"
            class="w-100"
            v-model="selectedBird"
          />
          <label for="year" class="sr-only">Year:</label>
          <dropdown-list
            id="year"
            class="ml-1 w-auto"
            :options="yearOptions"
            v-model="selectedYear"
          />
        </div>
        <figure class="legend m-0">
          <img :src="imageFilePath" :alt="selectedBird" />
          <figcaption>
            <p class="sr-only">{{ selectedBird }}</p>
            <span
              v-if="selectedCounty"
              class="figcaption_county"
              :style="{ borderLeftColor: highlightedCounty.activeColor }"
            >
              {{ highlightedCounty.county }}
            </span>
            <span v-if="selectedCounty" class="figcaption_percent">
              <span class="figcaption_data">
                {{ highlightedCounty[this.selectedYearColumn] }}%
              </span>
              of gardens
            </span>
          </figcaption>
        </figure>
      </div>
    </nav>
    <div class="loader" :class="{ 'is-paused fade': !this.loaded }">
      <div class="lds-ripple">
        <div></div>
        <div></div>
      </div>
    </div>
    <google-map
      name="bird-map"
      :countyData="countyData"
      :birdData="birdData"
      :selectedBird="selectedBird"
      :selectedYear="selectedYearColumn"
      @highlight="highlightCounty"
      @reset="resetCounty"
      @mapLoaded="mapIsLoaded"
      @mapLoading="mapIsLoading"
    />
    <button class="btn"><span class="sr-only">Zoom In</span></button>
    <button class="btn"><span class="sr-only">Zoom Out</span></button>
  </div>
</template>

<script>
import DropdownList from "./components/DropdownList.vue";
import birdData from "./assets/data/birddata.json";
import countyData from "./assets/data/counties.json";
import GoogleMap from "./components/GoogleMap";

export default {
  name: "App",
  components: {
    DropdownList,
    GoogleMap
  },
  data() {
    return {
      loaded: false,
      selectedCounty: null,
      selectedYear: "2022",
      birdData,
      countyData,
      birdOptions: [],
      selectedBird: "Robin",
      yearLookup: [
        {
          year: "2022",
          column: "percent_gardens_2022"
        },
        {
          year: "2021",
          column: "percent_gardens_2021"
        },
        {
          year: "2020",
          column: "percent_gardens_2020"
        },
        {
          year: "2019",
          column: "percent_gardens_2019"
        },
        {
          year: "2018",
          column: "percent_gardens_2018"
        }
      ]
    };
  },
  computed: {
    imageFilePath() {
      return require("./assets/images/" +
        this.selectedBird.replace(/\s+/g, "-").toLowerCase() +
        ".png");
    },
    highlightedCounty() {
      var highlighted = this.birdData.find(item => {
        return (
          item.county.match(this.selectedCounty) &&
          item.species.match(this.selectedBird)
        );
      });
      highlighted.activeColor = this.setColor(
        highlighted[this.selectedYearColumn]
      );
      return highlighted;
    },
    yearOptions() {
      return this.yearLookup.map(item => {
        return item.year;
      });
    },
    selectedYearColumn() {
      return "percent_gardens_" + this.selectedYear;
    }
  },
  methods: {
    highlightCounty(county) {
      this.selectedCounty = county;
    },
    resetCounty() {
      this.selectedCounty = null;
    },
    mapIsLoaded() {
      this.loaded = true;
    },
    mapIsLoading() {
      this.loaded = false;
    },
    setColor(percent) {
      if (percent > 95) {
        return "#990100";
      } else if (percent > 90) {
        return "#cc0001";
      } else if (percent > 85) {
        return "#fe0000";
      } else if (percent > 80) {
        return "#ff3300";
      } else if (percent > 75) {
        return "#ff6600";
      } else if (percent > 70) {
        return "#fe9900";
      } else if (percent > 65) {
        return "#ffcc00";
      } else if (percent > 60) {
        return "#fffc00";
      } else if (percent > 55) {
        return "#cbfe33";
      } else if (percent > 50) {
        return "#98ff66";
      } else if (percent > 45) {
        return "#67ff9a";
      } else if (percent > 40) {
        return "#33ffcc";
      } else if (percent > 35) {
        return "#00ffff";
      } else if (percent > 30) {
        return "#00ccff";
      } else if (percent > 25) {
        return "#0099ff";
      } else if (percent > 20) {
        return "#0166ff";
      } else if (percent > 15) {
        return "#0000fe";
      } else if (percent > 10) {
        return "#0033ff";
      } else if (percent > 5) {
        return "#0000cc";
      } else {
        return "#000098";
      }
    }
  },
  created() {
    var uniqueBirds = [];
    this.birdData.filter(bird => {
      if (uniqueBirds.indexOf(bird.species) === -1) {
        return uniqueBirds.push(bird.species);
      }
    });
    this.birdOptions = uniqueBirds.sort();
  }
};
</script>

